<template>
    <div class="electCalendar">
        <div class="header">
            <el-radio-group v-model="electType" class="radio" @change="electTypeChange">
                <el-radio label="1">用户</el-radio>
                <el-radio label="2">计量点</el-radio>
            </el-radio-group>
            <div class="header-right" v-if="electType==='2'">
                <el-select
                    v-model="measuringPoint"
                    class="select"
                    size="mini"
                    placeholder="请选择"
                    @change="measuringPointChange"
                >
                    <el-option
                        v-for="item in measuringPointOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                </el-select>
                <div class="unit">单位：度</div>
            </div>
            <div class="header-right" v-else>
                <el-input v-model="yhbh" class="input" size="mini" placeholder="请输入用户编号"></el-input>
                <el-button type="primary" size="mini" @click="yhbhSearch">查询</el-button>
            </div>
        </div>

        <el-calendar class="calendar" v-model="time" v-if="calendarShow">
            <template slot="header">111</template>

            <template slot="dateCell" slot-scope="{date, data}">
                <div class="calendar-item">
                    <div class="calendar-day">{{ data.day.split('-').slice(2).join('-') }}</div>
                    <div v-for="(item,index) in calendarData" :key="index" @click="clickItem(date,data)">
                        <div
                            class="calendar-value"
                            :style="{background:item.color}"
                            v-if="item.date===data.day"
                        >
                            <el-tooltip
                                class="calendar-tooltip"
                                effect="light"
                                :content="item.weather"
                                placement="top"
                            >
                                <div>{{item.value}}</div>
                            </el-tooltip>
                        </div>
                        <div v-else></div>
                    </div>
                </div>
            </template>
        </el-calendar>
        <div class="footer">
            <div class="current footer-item">
                <span>当前计量点累计电量：</span>
                <span>{{currentElectric}}</span>
            </div>
            <div class="total footer-item">
                <span>总累计电量：</span>
                <span>{{totalElectric}}</span>
            </div>
            <div class="button" @click="exportExcel">导出excel</div>
        </div>
    </div>
</template>

<script>
  // import { inter } from "config/interface";
  import Interface from '@/base/mixin/electricity-behavior';
  import { JC_PATH } from '@/config'

  export default {
    name: "electCalendar",
    mixins: [Interface],
    data() {
      return {
        //用电类型
        electType: "1",
        //用户编号
        yhbh: "",
        //计量点选项
        measuringPoint: "",
        measuringPointOptions: [],
        //日历当前时间
        time: null,
        //日历数据
        calendarData: [
          // { date: '2021-01-01', value: "45", weather: "12度", color: "" },
        ],
        //日历展示
        calendarShow: true,
        //当前累计电量
        currentElectric: 0,
        //总累计电量
        totalElectric: 0,
        //总累计电量
        allElectric:[
          // {month: "2020-12", elec: 20.2, type: "yhbh"},
        ]
      };
    },
    components: {
      // eleCalendar,
    },
    watch:{
      time: {//深度监听，可监听到对象、数组的变化 单选
        handler:function(val) {
          window.console.log(val)
          let month = '';
          if (val.getMonth()<10){
            month = '0'+(val.getMonth()+1)
          }else {
            month = val.getMonth()+1
          }
          window.console.log(val.getFullYear()+'-'+month)
          this.setAllElectric(val.getFullYear()+'-'+month)
        },
        deep: true, //true 深度监听
        immediate: true //开启首次监听赋值（不开启首次监听不到）
      },
    },
    mounted() {
      this.$nextTick(() => {
        // 点击上个月
        let prevBtn1 = document.querySelector('.el-calendar__button-group .el-button-group>button:nth-child(1)');
        prevBtn1.addEventListener('click', () => {
          console.log('上个月');
        })
        // 点击今天
        let prevBtn2 = document.querySelector('.el-calendar__button-group .el-button-group>button:nth-child(2)');
        prevBtn2.addEventListener('click', () => {
          console.log('今天');
        })
        // 点击下个月
        let prevBtn3 = document.querySelector('.el-calendar__button-group .el-button-group>button:nth-child(3)');
        prevBtn3.addEventListener('click', () => {
          console.log('下个月');
          console.log(this.time, '下个月');
        })
      })
    },
    methods: {
      //点击日历单元格
      clickItem(d1,d2){

      },
      //计算累计电量
      setAllElectric(date){
        this.currentElectric = ''
        this.totalElectric = ''
        for (let i=0;i<this.allElectric.length;i++){
          if (this.allElectric[i].type==='jld'&&this.allElectric[i].month===date){
            this.currentElectric = this.allElectric[i].elec
          }
          if (this.allElectric[i].type==='yhbh'&&this.allElectric[i].month===date){
            this.totalElectric = this.allElectric[i].elec
          }
        }

      },
      //改变用电类型
      electTypeChange(val) {
        this.measuringPoint = ''
        this.measuringPointOptions = []
        this.currentElectric = ''
        this.totalElectric = ''
        this.calendarData = []
        if (val === '1') {
          this.yhbhSearch()
        } else if (val === '2') {
          this.getMeasuringPointOptions()
        }
      },
      //处理数据颜色
      dealData(data) {
        this.calendarData.forEach(e => {
          let value = parseFloat(e.value)
          if (value > 0 && value <= 5) {
            e.color = '#CAF982'
          } else if (value > 5 && value <= 15) {
            e.color = '#FFFF80'
          }
          else if (value > 15 && value <= 25) {
            e.color = '#FBB48D'
          }
          else if (value > 25) {
            e.color = '#F54F23'
          }
        });
      },
      // 导出
      exportExcel() {
        if (this.electType==='1'){
          window.location.href= JC_PATH+'/elecCalendar/exportData?yhbh='+this.yhbh
        }else {
          window.location.href= JC_PATH+'/elecCalendar/exportData?jldbh='+this.measuringPoint
        }
      },
      //根据用户编号查询
      yhbhSearch() {
        let data = {
          yhbh: this.yhbh,
        }
        this.getCalendarData(data)
      },
      // 改变计量点
      measuringPointChange() {
        let data = {
          jldbh: this.measuringPoint,
        }
        this.getCalendarData(data)
      },
      //获取计量点列表
      getMeasuringPointOptions() {
        this.getAllJlPoint({

        }).then(res=>{
          let optionsData = []
          if (res && res.length !== 0) {
            let data = res

            data.forEach((e) => {
              optionsData.push({ label: e, value: e })
            })
            this.measuringPointOptions = optionsData
            window.console.log(this.measuringPointOptions,'kkkk')
          }
        })
        // this.bda_post(inter.electCalendarInfo.pointInfo, {}).then(res => {
        //   let optionsData = []
        //   if (res.data && res.data.length !== 0) {
        //     let data = res.data
        //     console.log(data, 'data');
        //     data.forEach((e) => {
        //       optionsData.push({ label: e, value: e })
        //     })
        //     this.measuringPointOptions = optionsData
        //   }
        // })
      },
      //获取日历信息
      getCalendarData(data) {
        this.calendarShow = false
        this.getData({
          yhbh: data.yhbh,
          jldbh: data.jldbh
        }).then(res=>{
          res.detail.forEach(e => {
            e.color = ""
          })
          this.calendarData = res.detail
          this.allElectric = res.totalElec
          this.time = new Date()
          this.dealData()

          this.calendarShow = true
        })
        // this.bda_post(inter.electCalendarInfo.electCalendarInfo, {
        //   yhbh: data.yhbh,
        //   jldbh: data.jldbh
        // }).then(res => {
        //   res.data.detail.forEach(e => {
        //     e.color = ""
        //   })
        //   this.calendarData = res.data.detail
        //   this.allElectric = res.data.totalElec
        //   this.time = new Date()
        //   this.dealData()
        //
        //   this.calendarShow = true
        //
        // })

      }
    },
  };
</script>

<style lang="less" >
    .electCalendar {
        .header {
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 10px;
            .radio {
                margin-left: 20px;
            }
            .header-right {
                margin-right: 20px;
                display: flex;
                align-items: center;
                .select {
                    margin-right: 10px;
                }
            }
        }
        .calendar {
            .el-calendar-day {
                padding: 0;
            }
            .el-calendar__body {
                padding: 12px;
            }
            .calendar-item {
                width: 100%;
                height: 100%;
                .calendar-day {
                    // text-align: center;
                    height: 25px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-bottom: 1px solid #ebeef5;
                }
                .calendar-value {
                    // text-align: center;
                    height: 25px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
                .calendar-tooltip {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
        .el-calendar-table .el-calendar-day {
            // height: calc((540px - 36px - 100px - 53px - 40px - 32px - 20px) / 5);
            height: 50px;
        }
        .footer {
            /*width: 100%;*/
            height: 32px;
            padding: 0 20px;
            display: flex;
            .footer-item {
                flex: 1;
                display: flex;
                align-items: center;
            }
            .current {
            }
            .total {
            }
            .button {
                // width: 32px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                // border: 1px solid #ebeef5;
            }
        }
    }
</style>
