<template>
  <div class="notifyConfig-form">
    <div class="nav">
      <el-select
        size="small"
        class="inputC"
        v-model="successListQueryForm.gddw"
        clearable
        placeholder="请选择供电单位"
        @change="dsjChange"
      >
        <el-option
          v-for="item in gddwOptions"
          :key="item.ORG_CODE"
          :label="item.ORG_NAME"
          :value="item.ORG_CODE"
        ></el-option>
      </el-select>
      <el-select
        v-model="xqjValue"
        clearable
        placeholder="请选择县区局"
        class="input_td"
        size="small"
        @change="qxjChange"
      >
        <el-option
          v-for="item in xqjOptions"
          :key="item.ORG_CODE"
          :label="item.ORG_NAME"
          :value="item.ORG_CODE"
        ></el-option>
      </el-select>
      <el-select
        size="small"
        class="input_td"
        v-model="successListQueryForm.gds"
        clearable
        placeholder="请选择供电所"
      >
        <el-option
          v-for="item in gdsOptions"
          :key="item.ORG_CODE"
          :label="item.ORG_NAME"
          :value="item.ORG_CODE"
        ></el-option>
      </el-select>
      <el-input class="inputC" placeholder="请输入用户编号" v-model="selectValue" size="small" clearable></el-input>
      <el-button size="small" type="primary" @click="getAlarmList">查询</el-button>
      <el-button size="small" type="primary" @click="clickSuccessList">成功执行电量告警通知推送任务客户清单查询</el-button>
    </div>
    <div class="content">
      <div class="content-item">
        <div class="content-title">
          <span class="block"></span>
          <span class="text">电量告警通知客户清单</span>
        </div>
        <div class="chart-main">
          <bdaTable
            :tableData="alarmData"
            :isPage="true"
            :pageConfig="alarmPageConfig"
            @changePage="changePage"
            @operate="operateList"
          ></bdaTable>
        </div>
      </div>
    </div>
    <el-dialog title="修改任务推送时间" :visible.sync="editTsTimeShow" width="40%" center>
      <div style="text-align: center;height: 100px">
        <el-date-picker
          size="small"
          style="width: 80%"
          v-model="tsTime"
          type="datetime"
          value-format="yyyy-MM-dd hh:mm:ss"
          placeholder="请选择时间"
        ></el-date-picker>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editTsTimeShow = false" size="small">取 消</el-button>
        <el-button type="primary" @click="setTsTime" size="small">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      class="successList-dialog"
      :visible.sync="successListVisible"
      width="80%"
      top="5vh"
      :before-close="successListHandleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '成功执行电量告警通知推送任务客户清单查询' }}</span>
      </span>
      <div class="dialog-content">
        <div class="dialog-content-title">
          <el-select
            size="small"
            class="inputC"
            v-model="successListQueryForm.gddw"
            clearable
            placeholder="请选择地市局"
            @change="dsjChange"
          >
            <el-option
              v-for="item in gddwOptions"
              :key="item.ORG_CODE"
              :label="item.ORG_NAME"
              :value="item.ORG_CODE"
            ></el-option>
          </el-select>
          <el-select
            v-model="xqjValue"
            clearable
            placeholder="请选择县区局"
            class="input_td"
            size="small"
            @change="qxjChange"
          >
            <el-option
              v-for="item in xqjOptions"
              :key="item.ORG_CODE"
              :label="item.ORG_NAME"
              :value="item.ORG_CODE"
            ></el-option>
          </el-select>
          <el-select
            size="small"
            class="input_td"
            v-model="successListQueryForm.gds"
            clearable
            placeholder="请选择供电所"
          >
            <el-option
              v-for="item in gdsOptions"
              :key="item.ORG_CODE"
              :label="item.ORG_NAME"
              :value="item.ORG_CODE"
            ></el-option>
          </el-select>
          <el-date-picker
            size="small"
            class="inputC"
            v-model="successListQueryForm.time"
            type="date"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            placeholder="请选择推送日期"
          ></el-date-picker>
          <el-input
            class="inputC"
            placeholder="请输入用户编号"
            v-model="selectValue"
            size="small"
            clearable
          ></el-input>
          <el-button size="small" type="primary" @click="getSuccessListData">查询</el-button>
        </div>
        <div class="dialog-content-main">
          <bdaTable
            :tableData="successListData"
            :isPage="true"
            :pageConfig="successListPageConfig"
            @changePage="changePage"
          ></bdaTable>
        </div>
      </div>
    </el-dialog>

    <el-dialog
      class="portraitInfo-dialog"
      :visible.sync="portraitInfoVisible"
      width="95%"
      top="5vh"
      :before-close="portraitInfoHandleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '客户用电日历画像信息' }}</span>
      </span>
      <div class="dialog-content">
        <div class="dialog-content-title">
          <el-input
            class="inputC"
            placeholder="请输入用户编号"
            v-model="portraitInfoQueryForm.yhbh"
            size="small"
            clearable
          ></el-input>
          <el-button size="small" type="primary" @click="yhbhQuery()">查 询</el-button>
        </div>
        <div class="dialog-content-main">
          <div class="recommend">
            <div class="main-title">推荐建议</div>
            <div class="text-item">
              <div class="text">{{'当前具有电价档级升档风险,准备发送电量告警短信通知,短信通知内容为:'}}</div>
              <div class="value">{{portraitInfoQueryForm.dxtznr}}</div>
            </div>
            <div class="text-item">
              <div class="text">{{'短信发送时间为:'}}</div>
              <div class="value">{{portraitInfoQueryForm.dxfssj}}</div>
            </div>
            <div class="text-item">
              <div class="text">{{'联系方式为:'}}</div>
              <div class="value">{{portraitInfoQueryForm.lxfs}}</div>
            </div>
          </div>
          <div class="portrait-label">
            <div class="main-content">
              <div class="main-title">画像标签</div>
              <div style="width: 100%;height: 500px">
                <word-cloud :wordCloudData="wordCloudData" v-if="wordCloudDataVisible"></word-cloud>
              </div>
            </div>
          </div>
          <div class="calendar">
            <div class="main-content">
              <elect-calendar ref="calendar"></elect-calendar>
            </div>
            <div class="base-info">
              <div class="title">本期用电预测信息</div>
              <el-table
                  :data="tableDataPower"
                  height="194"
                  border
                  style="width: 100%">
                <el-table-column
                    prop="ycrq"
                    label="预测日期"
                    width="180">
                </el-table-column>
                <el-table-column
                    prop="ycrqqbqljdl"
                    label="预期日期前本期累计电量"
                    width="180">
                </el-table-column>
                <el-table-column
                    prop="bqdlycz"
                    label="本期电量预测值">
                </el-table-column>
                <el-table-column
                    prop="ycbqdldj"
                    label="本期电量预测档级">
                </el-table-column>
              </el-table>
            </div>
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Interface from '@/base/mixin/electricity-behavior';
import bdaTable from '@/components/bda-table/bda-table'
import wordCloud from "./components/wordCloud";
import electCalendar from "./components/electCalendar";
export default {
  name: "notifyConfig-form",
  mixins: [Interface],
  components: {
    bdaTable,
    wordCloud,
    electCalendar,
  },
  props: {

  },
  data() {
    return {
      //搜索条件
      dfDatevalue: '',
      xqjValue: '',
      xqjOptions: [],
      // 告警列表
      alarmData: {},
      alarmQueryForm: {
        current: 1,
        size: 10,
      },
      editTsTimeShow: false,
      tsTime: '',
      dlgjtzkhId: '',
      //分页设置
      alarmPageConfig: {
        name: "alarm",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      selectValue: '',
      loading: false,
      // 成功列表
      successListData: {},
      successListVisible: false,
      successListQueryForm: {
        gddw: "",
        gds: "",
        time: "",
        yhbh: "",
        current: 1,
        size: 10,
      },
      //分页设置
      successListPageConfig: {
        name: "successList",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      gddwOptions: [],
      gdsOptions: [],
      // 画像信息
      portraitInfoVisible: false,
      portraitInfoQueryForm: {
        yhbh: "",
        dxtznr: '',
        dxfssj: "",
        lxfs: ""
      },
      wordCloudData: [],
      wordCloudDataVisible: false,
      //客户用电日立信息
      tableDataPower:[]

    }
  },
  mounted() {
    this.getAlarmList()
    this.GetPowerAreaCode()
  },
  methods: {
    //获取供电单位编码--地市局
    GetPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "2",//2==供电单位==地市局，3==县区局局，4===供电所
        // "orgCode":"0306" //上一级编码
      }).then(res => {
        this.gddwOptions = res
      })
    },
    //获取区县局--区县局
    GetQxjPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "3",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.successListQueryForm.gddw //上一级编码
      }).then(res => {
        this.xqjOptions = res
      })
    },
    //获取供电所
    GetGdsPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "4",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.xqjValue //上一级编码
      }).then(res => {
        this.gdsOptions = res
      })
    },
    //地市局选择
    dsjChange() {
      this.successListQueryForm.gds = ''
      this.gdsOptions = []
      this.xqjValue = ''
      this.xqjOptions = []
      this.GetQxjPowerAreaCode()
    },
    //区县局选择
    qxjChange() {
      this.successListQueryForm.gds = ''
      this.gdsOptions = []
      this.GetGdsPowerAreaCode()
    },
    GetRecommendations() {
      this.getRecommendations({
        "yhbh": this.portraitInfoQueryForm.yhbh
      }).then(res => {
        this.portraitInfoQueryForm.dxtznr = res.content
        this.portraitInfoQueryForm.lxfs = res.lxdh
        this.portraitInfoQueryForm.dxfssj = res.smsSendTime
      })
    },
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val
    },
    //改变分页
    changePage(data) {
      if (data.name === 'alarm') {
        this.alarmQueryForm.size = data.size
        this.alarmQueryForm.current = data.current
        this.getAlarmList()
      }
      if (data.name === 'successList') {
        this.successListQueryForm.size = data.size
        this.successListQueryForm.current = data.current
        this.getSuccessListData()
      }
    },
    // 操作列表
    operateList(type, data) {
      if (type === 'query') {
        this.dlgjtzkhId = data.id
        this.portraitInfoVisible = true
        this.portraitInfoQueryForm.yhbh = data.yhbh
        this.GetRecommendations()
        this.Getbqydxx()
        this.getWordCloudData()
        this.$nextTick(() => {
          this.$refs.calendar.yhbh = data.yhbh
          this.$refs.calendar.yhbhSearch()
        })
      }
      if (type === 'edit') {
        this.tsTime = ''
        this.editTsTimeShow = true
      }
      if (type === 'cancel') {
        this.$confirm('是否确定撤销?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteTask({
            "dlgjtzkhId": data.id
          }).then(() => {
            this.$message({
              type: 'success',
              message: '撤销成功!'
            });
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
        });
      }

    },
    //修改推送时间
    setTsTime() {
      this.modifySendToXyTime({
        "sendToYxTimePlan": this.tsTime,
        "dlgjtzkhId": this.dlgjtzkhId
      }).then(() => {

      })
      this.editTsTimeShow = false
    },
    // 成功执行电量告警通知推送任务客户清单查询
    clickSuccessList() {
      this.successListVisible = true
      this.getSuccessListData()
    },

    // 关闭成功
    successListHandleClose() {
      this.successListVisible = false
    },
    // 关闭画像信息
    portraitInfoHandleClose() {
      this.portraitInfoVisible = false
    },
    // 告警清单
    getAlarmList() {
      let columnNameData = [
        { prop: 'sss', label: '推送任务标识' },
        { prop: 'yhbh', label: '用户编号', width: 150 },
        { prop: 'lxdh', label: '联系号码' },
        { prop: 'dfny', label: '电费年月' },

        { prop: 'yhmc', label: '用户名称' },
        { prop: 'yddz', label: '用电地址' },
        { prop: 'dsj', label: '地市局' },
        { prop: 'xqj', label: '县区局' },
        { prop: 'gds', label: '供电所' },
        { prop: 'content', label: '短信发送内容', width: 300 },
        { prop: 'smsSendTime', label: '短信发送时间', width: 150 },
        { prop: 'sendToYxTimePlan', label: '计划推送至营销系统时间', width: 160 },
        {
          prop: "Operate",
          label: "操作",
          align: "center",
          width: "300px",
          type: [
            {
              name: "查询画像信息",
              action: "query"
            },
            {
              name: "修改任务推送时间",
              action: "edit"
            },
            {
              name: "撤销任务",
              action: "cancel"
            }
          ]
        }
      ]
      this.getNotifyCustomer({
        "dsj": this.successListQueryForm.gddw,
        "xqj": this.xqjValue,
        "gds": this.successListQueryForm.gds,
        "page": this.alarmQueryForm.current,
        "size": this.alarmQueryForm.size,
        "yhbh": this.selectValue
      }).then(res => {
        this.alarmPageConfig.total = res.total
        this.alarmData = {
          tableData: [
            ...res.records
          ],
          headerColor: '#1899fa',
          title: "告警清单",
          name: 'alarm',
          size: this.alarmQueryForm.size,
          current: this.alarmQueryForm.current,
          total: this.alarmPageConfig.total,
          columnNameData
        }
      })
    },
    //
    getSuccessListData() {
      let columnNameData = [
        { prop: 'id', label: '电量告警短信通知任务标识' },
        { prop: 'sendToYxTimePlan', label: '计划推送至营销系统时间' },
        { prop: 'sendToYxTime', label: '推送至营销系统时间' },
        { prop: 'dfny', label: '电费年月' },
        { prop: 'yhbh', label: '用户编号' },
        { prop: 'yhmc', label: '用户名称' },
        { prop: 'yddz', label: '用电地址' },
        { prop: 'dsj', label: '地市局' },
        { prop: 'xqj', label: '县区局' },
        { prop: 'gds', label: '供电所' },
        { prop: 'lxdh', label: '联系号码' },
        { prop: 'content', label: '短信发送内容', width: 300 },
        { prop: 'smsSendTime', label: '短信发送时间' }
      ]
      this.getSuccessTaskList({
        "dsj": this.successListQueryForm.gddw,
        "xqj": this.xqjValue,
        "gds": this.successListQueryForm.gds,
        "page": this.successListQueryForm.current,
        "size": this.successListQueryForm.size,
        "yhbh": this.selectValue,
        "tsrq": this.successListQueryForm.time,
      }).then(res => {
        this.loading = false
        this.successListPageConfig.total = res.total
        this.successListData = {
          tableData: [
            ...res.records
          ],
          headerColor: '#1899fa',
          title: "告警清单",
          name: 'successList',
          size: this.successListQueryForm.size,
          current: this.successListQueryForm.current,
          total: this.successListPageConfig.total,
          columnNameData
        }
      })

    },
    //本期用电预测信息
    Getbqydxx(){
      this.getbqydxx({
        yhbh:this.portraitInfoQueryForm.yhbh
      }).then(res=>{
        this.tableDataPower = res
      })
    },
    //获取画像信息
    getWordCloudData() {
      this.wordCloudDataVisible = false
      this.getUserPortraitInfo({
        yhbh: this.portraitInfoQueryForm.yhbh
        // yhbh: '32123'
      }).then(res => {
        let wordCloudData = []
        if (res && res.length !== 0) {
          res.forEach(e => {
            wordCloudData.push({
              name: e,
              value: parseInt(Math.random() * 100)
            })
          })
        }
        this.wordCloudData = wordCloudData
        this.wordCloudDataVisible = true
      })
    },
    yhbhQuery() {
      this.GetRecommendations()
      this.getWordCloudData()
      this.Getbqydxx()
    }
  }
}
</script>

<style scoped lang="scss">
.notifyConfig-form {
  width: 100%;
  height: 100%;
  ::v-deep .el-table {
    .success-row {
      background: rgb(244, 244, 244);
    }
    .cell {
      padding: 2px 4px;
    }
    th {
      background-color: rgb(24, 153, 250);
      .cell {
        color: #ffffff;
      }
    }
  }
  .inputC {
    width: calc(20% - 30px);
    min-width: 100px;
    margin: 0px 6px;
  }
  .input_td {
    width: calc(15%);
    margin: 0px 6px;
  }
  .nav {
    span {
      margin: 0px 8px 0px 30px;
    }
  }
  .content {
    width: 100%;
    height: calc(100% - 32px);
    display: flex;
    flex-wrap: wrap;
    .content-item {
      width: 100%;
      height: 100%;
      .content-title {
        display: flex;
        align-items: center;
        height: 30px;
        line-height: 30px;
        padding: 5px 0px 0px 5px;
        font-weight: bold;
        color: #1a84f4;
        .block {
          display: inline-block;
          width: 8px;
          height: 16px;
          margin: 0 10px;
          background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
        }
      }
      .chart-main {
        width: 100%;
        height: calc(100% - 35px);
        padding: 10px;
        box-sizing: border-box;
      }
    }
  }
  .successList-dialog,
  .portraitInfo-dialog {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .dialog-chart {
      height: 400px;
    }
  }
  .successList-dialog {
    .dialog-content-title {
      height: 42px;
      display: flex;
      align-items: center;
      margin-bottom: 10px;
    }
    .dialog-content-main {
      height: 540px;
    }
  }
  .portraitInfo-dialog {
    .dialog-content-title {
      height: 42px;
      display: flex;
      align-items: center;
      margin-bottom: 10px;
    }
    .dialog-content-main {
      height: 760px;
      display: flex;
      padding: 0 20px;
      .main-title {
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #0056d7;
        font-weight: bold;
      }
      .recommend {
        width: 20%;
        border: 1px solid #72ccff;
        margin-right: 20px;
        padding: 0 10px 10px;
        .text-item {
          display: block;
          margin-bottom: 20px;
          font-style: 16px;
          .text {
            display: block;
            margin-bottom: 10px;
            font-weight: bold;
            font-size: 15px;
          }
        }
      }
      .portrait-label {
        width: 40%;
        margin-right: 20px;
        .main-content {
          height: calc(100%);
          border: 1px solid #72ccff;
        }
      }
      .calendar {
        width: 50%;
        .main-content {
          height: calc(70% - 20px - 20px);
          margin-bottom: 20px;
          border: 1px solid #72ccff;
          padding: 10px 6px;
        }
        .base-info {
          height: 30%;
          border: 1px solid #72ccff;
          .title{
            padding-left: 18px;
            border-left: 6px solid #1A84F4;
            font-weight: bold;
            margin: 6px 0;
          }
        }
      }
    }
  }
}
</style>